.c-stockists {
  background: $s-color--background-subdued;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 3.75rem $s-gutter--edge-mobile 4.5rem;
    overflow: hidden;
  }

  &__title {
    text-align: center;
    margin-bottom: 0.875rem;
  }

  &__subtitle {
    text-align: center;
    margin-bottom: 2.0625rem;
  }

  &__content {
    text-align: center;
    max-width: 35rem;
    margin: 0 auto 2.5rem;
  }

  &__tabs {
    display: flex;
    position: relative;
    width: min-content;
    margin: 0 auto;
  }

  &__tab-blob {
    position: absolute;
    top: 0;
    left: 0;
    width: 9rem;
    height: 3.5rem;
    z-index: $s-z--reset;
    transition: transform 0.5s;
    transform: translateX(0);

    &.is-second-active {
      transform: translateX(100%);
    }
  }

  .o-accordion__tab-blob {
    display: block;
  }

  &__tab {
    color: $s-color--font-focus;
    width: 8.8125rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: $s-z--text-overlay;
    cursor: pointer;
    transition: all 0.2s;

    &.is-active {
      color: $s-color--font-invert;
    }
  }

  &__arrow-icon {
    margin-left: auto;
    width: 1.75rem;
    display: none;

    .is-overflowing & {
      display: block;
    }
  }

  &__store-wrapper {
    display: none;
    grid-row: 1/-1;
    grid-column: 1/-1;
    transition: all 0.2s;
    z-index: $s-z--reset;
    overflow: hidden;

    &.is-visible {
      display: block;
      z-index: $s-z--text-overlay;
    }
  }

  &__online-wrapper {
    display: none;
    grid-row: 1/-1;
    grid-column: 1/-1;
    transition: all 0.2s;
    z-index: $s-z--reset;

    &.is-visible {
      display: block;
      z-index: $s-z--text-overlay;
    }
  }

  &__store-name {
    margin-bottom: 0;
    font-size: 0.9375rem;
    font-weight: 400;
  }

  &__store-info {
    margin-bottom: 2.5rem;

    &:last-child {
      margin-bottom: 0;
    }

    p {
      margin-bottom: 0;
      font-size: 0.9375rem;
      font-weight: 400;
    }

    a {
      color: $s-color--font-focus;
      margin-right: 1rem;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__store {
    margin: 2.5rem 0;
  }

  &__countries-categories {
    display: flex;
    gap: 2rem;
    margin-top: 4.0625rem;
    overflow: auto;
    justify-content: center;
    margin-bottom: 2.3rem;
    scrollbar-width: none;
    scrollbar-color: $s-color--background-subdued $s-color--background-subdued;
    
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      display: none;
    }

    &::-webkit-scrollbar-track {
      background: $s-color--background-subdued;
      height: 0;
      width: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: $s-color--background-subdued;
      height: 0;
      width: 0;
    }

    .is-overflowing & {
      justify-content: start;
    }
  }

  .is-overflowing &__country:last-child {
    margin-right: 2rem;
  }

  .is-overflowing &__countries-categories-wrapper {
    position: relative;

    &::after {
      content: "";
      display: inline-block;
      position: absolute;
      height: 100%;
      top: 0;
      right: 0;
      width: 2.5rem;
      background: linear-gradient(to right, transparent, $s-color--background-subdued);
    }
  }


  &__state-wrapper {
    &:first-of-type {
      border-top: 1px solid $s-color--border-20;
    }
  }

  &__country {
    white-space: nowrap;
    cursor: pointer;
    display: inline-block;
    transition: all 0.2s;

    &.is-active {
      color: $s-color--font-focus;
      border-bottom: 1px solid currentColor;
    }
  }

  &__accordion-content {
    padding-bottom: 4rem;
    margin-top: 0.1875rem;
  }

  &__countries-wrappers {
    margin-top: 1.375rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  &__country-wrapper {
    display: none;
    transition: all 0.2s;
    grid-row: 1/-1;
    grid-column: 1/-1;
    z-index: $s-z--reset;

    &.is-visible {
      display: block;
      z-index: $s-z--text-overlay;
    }
  }

  &__state {
    padding: 2rem 0;
  }

  &__main-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  @include t-media-query($s-small-up) {
    &__inner {
      padding: 3.75rem 2.75rem 4.5rem;
    }

    &__accordion-content {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }

    &__country-wrapper-inner {
      display: grid;
      margin-top: 2rem;
      grid-template-columns: 1fr 1fr;
    }

    &__store-info {
      margin: 0;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 6.1875rem 15% 9.9375rem;
    }

    .o-accordion__label {
      text-align: left;
    }

    &__subtitle {
      font-size: 3rem;
      margin-bottom: 3.4375rem;
    }

    &__accordion-content {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 2.5rem;
      padding-bottom: 4rem;
    }

    &__country-wrapper-inner {
      display: grid;
      grid-gap: 2.5rem;
      grid-template-columns: repeat(3, 1fr);
      margin-top: 2.5rem;
    }

    &__tabs {
      gap: 1.5rem;
    }

    &__tab-blob {
      width: 11.9375rem;
      height: 4.75rem;

      &.is-second-active {
        transform: translateX(110%);
      }
    }

    &__tab {
      width: 11.9375rem;
      height: 4.75rem;
      font-size: 1.25rem;
    }

    &__content {
      margin-bottom: 4.375rem;
    }

    &__country {
      font-size: 1.25rem;
    }

    &__countries-categories {
      gap: 8.8%;
      margin-top: 4.5rem;
    }

    &__countries-wrappers {
      margin-top: 4.0625rem;
    }

    &__state {
      padding: 3.5rem 0;
    }

    &__state-name {
      top: 52%;
    }

    .o-accordion__plus,
    .o-accordion__minus {
      display: block;
      width: 1.4375rem;
      height: 1.4375rem;
      right: 0.5rem;
      top: 54%;
    }
  }

  @include t-media-query($s-large-up) {
    &__accordion-content {
      width: 74.2%;
      margin-left: auto;
      grid-row-gap: 3.4375rem;
      margin-top: -0.0625rem;
    }

    &__tab {
      font-size: 1.5rem;
    }

    &__country {
      font-size: 1.5rem;
    }

    &__country-wrapper-inner {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding: 6.1875rem 21% 9.9375rem;
    }
  }
}